import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PostImg from './PostImg';

let lastScrollY = 0;
let ticking = false;

export default class PostImgColored extends Component {
    constructor(props) {
        super(props);
        this.ref = React.createRef();

        this.startPercentage = 0.6;
        this.endPercentage = 0.2;
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
      lastScrollY = window.scrollY;

        if (!ticking) {
            window.requestAnimationFrame(() => {
                console.log('hello');
                console.log(this.getPercentageHeight());
                ticking = false;
            });

            ticking = true;
        }
    };

    getPercentageHeight = () => {
        const viewport = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);

        const elementOffset = this.ref.current.getBoundingClientRect().top;

        return elementOffset/viewport;
    };

    render() {
        const { props } = this;
        const { src } = props;

        return (
            <div ref={this.ref}>
                <PostImg src={src} {...props} />
            </div>
        );
    }
}

PostImgColored.propTypes = {
    src: PropTypes.string.isRequired,
    bgColor: PropTypes.string.isRequired,
};

